export default {
	data() {
		return {
			comments : [],
			clength : 0
		}
	},
	onLoad:function(){
		this.getComments()
	},
	methods: {
		gos(){
			uni.redirectTo({
				url: 'Details'
			});
		},
		mores(){
			console.log(111)
		},
		getComments(){
			var _thas = this;
			uni.request({
				url: 'http://news-at.zhihu.com/api/4/story/4232852/short-comments',
				method: 'GET',
				data: {},
				success: res => {
					console.log(res.data.comments);
					_thas.clength = res.data.comments.length
					for (var i=0 ; i<_thas.clength ; i++ ) {
						_thas.comments.push(res.data.comments[i])
					}
					_thas.getCommentss();
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getCommentss(){
			var _thas = this;
			uni.request({
				url: 'http://news-at.zhihu.com/api/4/story/4232852/long-comments',
				method: 'GET',
				data: {},
				success: res => {
					console.log(res.data.comments);
					var leng = res.data.comments.length
					for (var i=0 ; i<leng ; i++ ) {
						_thas.comments.push(res.data.comments[i])
					}
					_thas.clength = _thas.clength+leng;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		praise(){
			console.log(111);
		},
		replay(){
			uni.redirectTo({
				url: 'Replay'
			});
		},
	},
	
}
